<template>
	<div>
		<top></top>
		<div class="con">
			<p class="ap">
				<span></span>
			</p>
		</div>
		<div class="contain">
			
			<Form ref="formItem" :model="formItem" :rules="ruleForm" :label-width="70">
				<FormItem label="" prop="pName">
					<Input type="text" v-model="formItem.pName" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="pIdNum">
					<Input type="text" v-model="formItem.pIdNum" placeholder=""></Input>
				</FormItem>

				<FormItem label="" prop="pAge">
					<Input type="text" v-model="formItem.pAge" placeholder=""></Input>
				</FormItem>
				<FormItem label="" prop="pSex">
					<Select v-model="formItem.pSex">
						<Option v-for="item in sexs" :value="item.value" :key="item.value">{{ item.value }}</Option>
					</Select>
				</FormItem>
				<FormItem label="" prop="pDept">
					<Select v-model="formItem.pDept">
						<Option v-for="item in department" :value="item.dName" :key="item.dName">{{ item.dName }}</Option>
					</Select>
				</FormItem>
				<FormItem label="">
					<i-switch v-model="formItem.ifMedical" @on-change="change">
						<span slot="open"></span>
						<span slot="close"></span>
					</i-switch>
				</FormItem>
				<FormItem label="">
					<Input v-model="formItem.medicalDescription" type="textarea" :autosize="{minRows: 6,maxRows: 6}" placeholder=""></Input>
				</FormItem>
				<FormItem>
					<Button style="float:right" type="primary" @click="submit('formItem')"> </Button>
				</FormItem>
			</Form>
		</div>
	</div>
</template>

<script>
import top from './../components/top.vue'
import cookie from './../tools/cookie.js'
export default {
	name: 'index',
	data () {
		return {
			formItem:{
				pName:'',
				pIdNum:'',
				pSex:'',
				pAge:'',
				pDept:'',
				medicalDescription:'',
				ifMedical:''
			},
			ruleForm:{
				pName: [
				{ required: true, message: '', trigger: 'blur' }
				],
				pIdNum: [
				{ required: true, message: '', trigger: 'blur' }
				],
				pAge:[
				{ required: true, message: '', trigger: 'blur' }
				],
				pSex: [
				{ required: true, message: '', trigger: 'blur' }
				],
				pDept:[
				{ required: true, message: '', trigger: 'blur' }
				]
			},
			sexs:[
			{
				value:''
			},
			{
				value:''
			},
			],
			department:[
			{
				dName:''
			},
			{
				dName:''
			}
			]
		}
	},
	methods: {
		submit(name) {
			this.$refs[name].validate((valid) => {
				if (valid) {
					cookie.setCookie('hasLogin',1,1);
					this.$router.push({path:'/information'})
				} else {
					this.$Message.error('');
				}
			})
		},
		change(index){
			console.log(index)
		},
		getData(){
			let hasLogin=cookie.getCookie('hasLogin');
			if(hasLogin==1){
				this.$router.push({path:'/information'})
			}
		}
	},
	mounted:function(){
		this.getData()
	},
	components:{
		top
	}
}
</script>

<style lang="css" scoped>
.contain{
	width: 100%;
	padding: 30px;
	padding-top: 20px;
	padding-bottom: 0px
}
.con{
	width: 100%;
	height: 60px;
	background-color: #96d3d1;
}
.con .ap{
	color: #ed3f14;
	text-align: center;
	line-height: 60px;
	font-family: ;
	font-size: 18px;
	font-weight: 700
}
</style>


