import Vue from "vue";
import VueRouter from "vue-router";
import index from './../view/index.vue'
import information from './../view/information.vue'


Vue.use(VueRouter);

const router = new VueRouter({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/information',
      name: 'information',
      component: information
    },
    ]
})
export default router;


