<template>
	<div>
		<top></top>
		<div class="contain">
			<div class="con">
				<p class="num">{{pCardNum}}</p>
				<p class="item">
					<label class="iTiltle"></label>
					<span class="iCon">{{pName}}</span>
				</p>
				<p class="item">
					<label class="iTiltle"></label>
					<span class="iCon">{{pDept}}</span>
				</p>
				<p class="item">
					<label class="iTiltle"></label>
					<span class="iCon">{{pConsultingRoom}}</span>
				</p>
			</div>
			<div class="Atime">
				<Steps :current="0" direction="vertical">
					<Step title="" :content="items[0]"></Step>
					<Step title="" :content="items[1]"></Step>
					<Step title="" :content="items[2]"></Step>
					<Step title="" :content="items[3]"></Step>
					<Step title="" :content="items[4]"></Step>
				</Steps>
			</div>
			<div class="leave">
				<Button type="error" @click="leave"></Button>
			</div>
		</div>
		<Modal v-model="modal" :styles="{top: '40%'}">
			<p slot="header" style="color:#f60;text-align:center">
				<Icon type="information-circled"></Icon>
				<span></span>
			</p>
			<div style="text-align:center">
				<p></p>
				<p></p>
			</div>
			<div slot="footer">
				<Button type="error" size="large" long  @click="dLeave"></Button>
			</div>
		</Modal>
	</div>

</template>

<script>
import cookie from './../tools/cookie.js'
import top from './../components/top.vue'
export default {
	name: 'information',
	data () {
		return {
			pCardNum:152,
			pName:'',
			pDept:'',
			pConsultingRoom:'',
			items:[
			'',
			`x`,
			'',
			'',
			''
			],
			modal:false
		}
	},
	methods:{
		getData(){
			let hasLogin=cookie.getCookie('hasLogin');
			if(hasLogin!=1){
				this.$router.push({path:'/'})
			}
		},
		leave(){
			this.modal=true
		},
		dLeave(){
			this.modal=false;
			cookie.clearCookie()
			this.$router.push({path:'/'})
		}
	},
	mounted:function(){
		this.getData()
	},
	components:{
		top
	}
}
</script>

<style lang="css" scoped>
.contain{
	width: 100%;
	position: right
}
.con{
	width: 100%;
	height: 100px;
	background-color: #96d3d1;
	padding-top: 0px
}
.Atime{
	width: 100%;
	padding: 40px;
	padding-bottom: 0;
	position: absolute;
	top: 160px;
	bottom: 50px;
	left: 0;
	right: 0
}
.ivu-steps,.ivu-steps-vertical{
	height: 100%
}
.ivu-steps-item{
	height: 20%
}
.time{
	font-size: 14px;
	font-weight: bold;
}
.content{
	padding-left: 5px;
}
.num{
	width: 60px;
	height: 60px;
	border-radius: 30px;
	background-color: #5e7575;
	text-align: center;
	line-height: 60px;
	font-size: 38px;
	font-family: ;
	color: #96d3d1;
	float: right;
	margin-top: 20px;
	margin-right: 20px
}
.item{
	color: #fff;
	font-size: 20px;
	font-family: ;
	height: 20px;
	float: left;
	width: 300px;
	margin-top: 8px;
	margin-left: 20px
}
.iTiltle{
	font-weight: 700;
}
.leave{
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	height: 50px;

}
.leave button{
	float: right;
	margin-right: 20px
}
</style>


